*{
    margin: 0;
    padding: 0;
}

/* Header Section */

.navbar{
    background-color: #F55A5A;
    width: 100%;
    /* Main Axis Row */
    display: flex; 
    padding: 15px 0;
    /* Centering Horizontally */
    justify-content: center; 
    /* Centering Vertically */
    align-items: center; 
    color: white;
}

.nav-font-awesome{
    font-size: 25px;
    padding: 5px;
}

.nav-header{
    padding: 5px;
}

/* Body Section */

.card{
    margin: 5% 10%;
    display: flex;
}

.card-img{
    width: 300px;
    margin: 5% 3%;
}

.card-features {
    width: 100%;
    position: relative;
    margin: 5% 0;
    color: #2B283A;
    align-items: center;
}

.card-features a{
    text-decoration-color: grey;
    margin: 0 50px;
}

.card-features a p{
    color: grey;
}

.card-location h3{
    font-family: 'Athiti', sans-serif;
    letter-spacing: 3px;
}

.card-location{
    display:flex;
    align-items: center;
}

.card-font-awesome{
    padding-right: 10px;
    color: #F55A5A;
}

.card-description{
    display: flex;
    flex-direction: column;
    position: absolute;
}

.card-description h1{
    margin-bottom: 10px;
}

.card-description > *{
    padding: 10px 0;
    font-family: 'Inter', sans-serif;
}

.card-description > p{
    font-weight: 400;
}

@media only screen and (max-width: 768px){
    .card-img{
        margin: 5% auto;
        width: 100%;
    }
    .card{
        display: flex;
        flex-direction: column;
    }
    .card-description{
        position: relative;
        text-align: center;
    }
    .card-location{
        width: 100%;
        justify-content: center;
        margin-bottom: 20px;
    }
    .card-font-awesome{
        margin: 0 0 0 auto;
    }

    .card-location a{
        margin: 0 auto;
    }
}